import React from "react";
import {NavLink} from 'react-router-dom'
import '../src/index.css';

const NavBar = () => {
  return (
    <>
      <div className="container-fluid nav_bg">
        <div className="row">
          <div className="col-10 mx-auto">
            <nav className="navbar navbar-expand-lg navbar-light bg-whilte">
              <NavLink className="navbar-brand" to="/">
                Adarsh
              </NavLink>
              <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>

              <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                  <li className="nav-item active">
                    <NavLink  activeClassName='menu_active' 
                    exact
                    className="nav-link active" to="/">
                      Home <span className="sr-only">(current)</span>
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink activeClassName='menu_active' className="nav-link" to="/service">
                      Services
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink activeClassName='menu_active' className="nav-link" to="/about">
                      About
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink activeClassName='menu_active' className="nav-link" to="/contact">
                      Contact
                    </NavLink>
                  </li>

                </ul>
                
              </div>
            </nav>
          </div>
        </div>
      </div>
    </>
  );
};

export default NavBar;
